.calculator{
	margin: 0 auto;
	width: 308px;
	background-color: #ddd;
	border: 1px solid #aaa;
	-webkit-box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
	box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);    
}

.calculator__previous-input, .calculator__current-input{
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	text-align: right;
	padding-right: 10px;
}

.calculator__previous-input{
	font-size: 16px;	
	padding-top: 10px;
	color: #555;
	height: 35px;
}

.calculator__current-input {
	font-size: 32px;
	height: 40px;
}

.calculator__btn-wrapper{
	list-style: none;
	padding: 0;
	margin: 0;
	font-size: 22px;
	display: grid;
	grid-template-columns: 75px 75px 75px 75px;
	grid-template-rows: 75px 75px 75px 75px 75px;
	grid-row-gap: 2px;
	grid-column-gap: 2px;
}

.calculator button {
	text-align: center;
	line-height: 75px;
	cursor: pointer;
	border: 2px solid transparent;
}

.calculator button:hover{
	background-color: #ddd;
	border-color: #ccc;
}

.calculator__btn-number{
	background-color: #f2f2f2;
}

.calculator__btn-operator{
	background-color: #e8e8e8;
}

.calculator__btn-operator--remove-digit{
	-webkit-transform: scale(-1, 1);
	    -ms-transform: scale(-1, 1);
	        transform: scale(-1, 1);
}



